<template>
	<v-app>
		<v-main class="mx-auto mt-10">
			<div v-if="reveal">
				<v-card
					flat
					min-width="334"
				>
					<v-card-title class="text-center flex-column  align-center">
						<v-avatar size="56">
							<img
								alt="Avatar"
								src="/images/avatar.png"
							>
						</v-avatar>
						<h1 class="font-weight-light text-h6 basil--text mt-4 ">
							
						</h1>
					</v-card-title>
				</v-card>
				<v-card
					flat
					outlined
				>
					<v-tabs
						v-model="tab"
						fixed-tabs
					>
						<v-tabs-slider color="bg_success"></v-tabs-slider>
						<v-tab
							v-for="item in items"
							:key="item"
							active-class="cuifan"
						>
							{{ item }}
						</v-tab>
					</v-tabs>
					<v-tabs-items v-model="tab">
						<v-form
							ref="form"
							v-model="valid"
							lazy-validation
							class="mx-6 mt-6"
						>
							<v-text-field
								v-model="name"
								:rules="nameRules"
								label=""
								required
							></v-text-field>
							<div>
								<v-row>
									<v-col cols="7">
										<v-radio-group
											v-model="sex"
											row
										>
											<span style="color:#666"></span>
											<v-radio
												label=""
												value="m"
											></v-radio>
											<v-radio
												label=""
												value="w"
											></v-radio>
										</v-radio-group>
									</v-col>
									<v-col cols="5">
										<v-select
											style="width:200px"
											:items="institutes"
											v-model="coid"
											label=""
											class="mt-3"
											dense
											outlined
											max-width="160"
										></v-select>
									</v-col>
								</v-row>
							</div>
							<v-text-field
								v-model="phone"
								:counter="11"
								:rules="phoneRules"
								label=""
								required
							></v-text-field>
							<v-text-field
								v-model="email"
								:rules="emailRules"
								label=""
								required
							></v-text-field>
							<v-text-field
								v-model="password"
								:append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
								:rules="passwordRules"
								label=""
								:type="show ? 'text' : 'password'"
								@click:append="show = !show"
								required
							></v-text-field>
							<v-text-field
								v-model="password1"
								:append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
								:rules="passwordRules1"
								label=""
								:type="show1 ? 'text' : 'password'"
								@click:append="show1 = !show1"
								required
							></v-text-field>
							<v-checkbox
								v-model="agreeTreaty"
								:rules="[v => !!v || '']"
								label=""
								required
							>
							</v-checkbox>
							<div class="justify-center d-flex my-4">
								<v-btn
									:disabled="!valid"
									color="success"
									class="mr-4"
									@click="toRegist"
								>
									
								</v-btn>
								<v-btn
									color="error"
									class="mr-4"
									@click="$refs.form.reset()"
								>
									
								</v-btn>
							</div>
						</v-form>
					</v-tabs-items>
				</v-card>
				<v-card
					flat
					outlined
					class="mt-4"
				>
					<v-card-text class="text-center"> <a
							href="/login"
							style="color:#58d8cb!important"
						></a></v-card-text>
				</v-card>
			</div>
			<div v-else>
				<v-card class="pb-4">
					<v-card-title><span class="account">{{account}}</span> &nbsp;, ~</v-card-title>
					<v-card-actions class="d-flex justify-center">
						<v-btn
							@click="$router.push('/login')"
							color="success"
							class="mr-4"
						>
							
						</v-btn>
						<v-btn
							color="success"
							class="mr-4"
							@click="reveal=!reveal"
						>
							
						</v-btn>
					</v-card-actions>
				</v-card>
			</div>
		</v-main>
	</v-app>
</template>

<script>
import { tregist, sregist } from '@/api/Login'
export default {
	data() {
		return {
			// 
			reveal: true,
			account: '123456',
			// 
			name: '',
			sex: 'm',
			institutes: ['', '', ''],
			coid: '',
			phone: '',
			email: '',
			password: '',
			password1: '',
			agreeTreaty: false,
			// 
			show: false,
			show1: false,
			// tab
			tab: '',
			items: ['', ''],
			// 
			valid: true,
			// 
			nameRules: [
				(v) => !!v || '',
				(v) => /^[\u4e00-\u9fa5a-zA-Z0-9]+$/.test(v) || '',
				(v) => (v && v.length <= 30) || '',
			],
			phoneRules: [
				(v) => !!v || '',
				(v) => /^\d+$/.test(v) || '',
				(v) => (v && v.length === 11) || '11',
			],
			emailRules: [
				(v) => !!v || '',
				(v) =>
					/^[1-9]\d{4,10}@qq\.com$/.test(v) || 'QQ',
			],
			passwordRules: [
				(v) => !!v || '',
				(v) =>
					/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,20}$/.test(v) ||
					' 8~20',
			],
			passwordRules1: [(v) => (v && v === this.password) || ''],
		}
	},
	methods: {
		toRegist() {
			// 
			if (!this.$refs.form.validate()) return
			let status = ['s', 't']
			let regist = [sregist, tregist][this.tab]
			regist({
				[status[this.tab] + 'name']: this.name,
				[status[this.tab] + 'sex']: this.sex,
				[status[this.tab] + 'phone']: this.phone,
				coid: this.institutes.findIndex((institute) => institute === this.coid),
				psw: this.password,
				email: this.email,
			}).then((res) => {
				if (res.data.state === false) return
				this.account = res.data
				this.$message({
					type: 'success',
					message: '',
				})
				this.reveal = false
			})
		},
	},
}
</script>
<style>
.cuifan {
	color: #4bd5c7 !important;
}
.account {
	color: #4bd5c7;
}
</style>